function TabPanel(props) {

  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      <Box p={3} justifyContent="center">{children}</Box>
    </Typography>
  );
}
TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};
function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}


function MenuTabs(props) {
  const [value, setValue] = React.useState(2);

  function getWindowDimensions() {
    const { innerWidth: width, innerHeight: height } = window;
    return width < 500 ? true : false;
  }
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <div >
      <Box  >
        <Tabs
          className="tab"
          indicatorColor="primary"
          textColor="primary"
          value={value}
          onChange={handleChange}
          variant="fullWidth"
          aria-label="full width tabs example"
        >
          <Tab label={ getWindowDimensions() ? null : <strong>Vida al Calamot</strong>} icon={iKiwi} {...a11yProps(0)} />
          <Tab label={ getWindowDimensions() ? null : <strong>Corredor natural</strong>} icon={iLeaf} {...a11yProps(1)} />
          <Tab label={ getWindowDimensions() ? null :  <strong>Salvem el Calamot</strong>} icon={iHike} {...a11yProps(2)} />
          <Tab label={ getWindowDimensions() ? null : <strong> Pla de ponent</strong>} icon={iBuilding} {...a11yProps(3)} />
          <Tab label={ getWindowDimensions() ? null : <strong color="disabled">Especulació inmobiliaria</strong>} icon={iFund} {...a11yProps(4)} />

        </Tabs>
      </Box>

      <Box>

        <TabPanel value={value} index={0}>
        <Grid container justify = "center">
          <h2>
            <ReactTypingEffect
                staticText=""
                text="La vida al Calamot"
                speed={200}
                eraseDelay="90000"
            />
          </h2>
        </Grid>
        <Grid container spacing={3} alignItems="center" justify="center">
          <Vida/>
        </Grid>
        <Grid container >
          <Grid item xs={12}>
            <h3 className="heading yellow"> </h3>
            <Footer/>
          </Grid>
        </Grid>
        </TabPanel>

        <TabPanel value={value} index={1}>
          <SecondTab />
          <Grid container >
            <Grid item xs={12}>
              <h3 className="heading yellow"> </h3>
              <Footer/>
            </Grid>
          </Grid>
        </TabPanel>

        <TabPanel value={value} index={2}>
          <ThirdTab />
          <Grid container >
            <Grid item xs={12}>
              <h3 className="heading yellow"> </h3>
              <Footer/>
            </Grid>
          </Grid>
        </TabPanel>

        <TabPanel value={value} index={3}>
          <FourthTab />
          <Grid container >
            <Grid item xs={12}>
              <h3 className="heading yellow"> </h3>
              <Footer/>
            </Grid>
          </Grid>
        </TabPanel>

        <TabPanel value={value} index={4}>
          <FifthTab />
        </TabPanel>

      </Box>


    </div>
  );
}

